import { FORMS_SECURITY_USER_GROUP_WORKSPACE_CONTEXT } from "./security-user-group-workspace.context-token.js";
import { UmbElementMixin } from "@umbraco-cms/backoffice/element-api";
import {
  LitElement,
  css,
  customElement,
  html,
  property,
  state,
} from "@umbraco-cms/backoffice/external/lit";

const elementName = "forms-security-user-group-workspace-editor";

@customElement(elementName)
export class FormsSecurityUserGroupWorkspaceEditorElement extends UmbElementMixin(
  LitElement,
) {
  @state()
  private _name = "";

  @property({ type: String, attribute: false })
  workspaceAlias?: string;

  #workspaceContext?: typeof FORMS_SECURITY_USER_GROUP_WORKSPACE_CONTEXT.TYPE;

  constructor() {
    super();

    this.consumeContext(
      FORMS_SECURITY_USER_GROUP_WORKSPACE_CONTEXT,
      (context) => {
        this.#workspaceContext = context;
        this.#observeId();
      },
    );
  }

  #observeId() {
    if (!this.#workspaceContext) return;
    this.observe(
      this.#workspaceContext.data,
      (data) => (this._name = data?.name ?? ""),
    );
  }

  render() {
    return html` <umb-workspace-editor
      alias="Forms.Workspace.Security.UserGroup"
    >
      <uui-input
        id="name"
        slot="header"
        readonly
        label="User Group"
        .value=${this.localize.term("formSecurity_manageIndividualFormsLabel") +
        " " +
        this._name}
      ></uui-input>
    </umb-workspace-editor>`;
  }

  static styles = [
    css`
      :host {
        display: block;
        width: 100%;
        height: 100%;
      }

      #name {
        flex: 1 1 auto;
      }
    `,
  ];
}

export default FormsSecurityUserGroupWorkspaceEditorElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsSecurityUserGroupWorkspaceEditorElement;
  }
}
