import { FORMS_SECURITY_USER_GROUP_ENTITY_TYPE } from "../entities.js";
import { FormsSecurityUserGroupDetailRepository } from "../repository/index.js";
import { FORMS_SECURITY_USER_GROUP_WORKSPACE_ALIAS } from "./manifests.js";
import { FormsSecurityUserGroupWorkspaceEditorElement } from "./security-user-group-workspace-editor.element.js";
import type { FormsSecurityWorkspaceContext } from "./security-workspace-base.context.js";
import { FormsSecurityWorkspaceBaseContext } from "./security-workspace-base.context.js";
import type { FormSecurityForGroup } from "@umbraco-forms/generated";
import type { UmbControllerHostElement } from "@umbraco-cms/backoffice/controller-api";
import type { UmbSubmittableWorkspaceContext } from "@umbraco-cms/backoffice/workspace";

export class FormsSecurityUserGroupWorkspaceContext
  extends FormsSecurityWorkspaceBaseContext<FormSecurityForGroup>
  implements UmbSubmittableWorkspaceContext, FormsSecurityWorkspaceContext
{
  constructor(host: UmbControllerHostElement) {
    super(
      host,
      FORMS_SECURITY_USER_GROUP_WORKSPACE_ALIAS,
      FORMS_SECURITY_USER_GROUP_ENTITY_TYPE,
      FormsSecurityUserGroupDetailRepository,
      FormsSecurityUserGroupWorkspaceEditorElement,
    );
  }

  setUserGroupSecurityProperty(alias: string, value: boolean) {
    if (!this._data.value) return;

    const userGroupSecurity = structuredClone(
      this._data.value.userGroupSecurity,
    );

    userGroupSecurity[alias] = value;
    this._data.update({ userGroupSecurity });
  }
}

export const api = FormsSecurityUserGroupWorkspaceContext;
